"use client";

import { useState } from "react";
import Link from "next/link";
import { motion } from "framer-motion";
import { Product, Language } from "@/lib/types";
import { Sparkles, Search } from "lucide-react";
import Image from "next/image";

const LANG_FLAGS: Record<Language, string> = { en: "ðŸ‡¬ðŸ‡§", fr: "ðŸ‡«ðŸ‡·", tr: "ðŸ‡¹ðŸ‡·" };

export function ProductDetailClient({ product }: { product: Product }) {
    const [lang, setLang] = useState<Language>("fr");
    const [selectedImage, setSelectedImage] = useState(product.images[0] || "");

    return (
        <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ duration: 0.7 }}
            className="bg-background-light dark:bg-background-dark min-h-screen"
        >
            <section className="w-full px-6 md:px-12 py-12 md:py-20 grid grid-cols-1 lg:grid-cols-12 gap-16">
                {/* Gallery Section */}
                <div className="lg:col-span-7 flex flex-col md:flex-row gap-6">
                    {/* Thumbnails - Left */}
                    <div className="order-2 md:order-1 flex md:flex-col gap-4 overflow-x-auto md:overflow-y-auto max-h-[600px] scrollbar-hide py-2">
                        {product.images.map((img, idx) => (
                            <button
                                key={idx}
                                onClick={() => setSelectedImage(img)}
                                className={`relative w-16 h-16 md:w-20 md:h-20 shrink-0 rounded-xl overflow-hidden border-2 transition-all p-1 bg-white dark:bg-gray-800 ${selectedImage === img ? "border-primary shadow-lg shadow-primary/10" : "border-gray-100 dark:border-gray-700 hover:border-gray-300"}`}
                            >
                                <img src={img} alt={`${product.title} ${idx + 1}`} className="w-full h-full object-contain" />
                            </button>
                        ))}
                    </div>

                    {/* Main Preview - Right */}
                    <div className="order-1 md:order-2 flex-1 relative group aspect-square md:aspect-4/5 bg-white dark:bg-gray-900 rounded-[32px] border border-gray-100 dark:border-gray-800 overflow-hidden flex items-center justify-center p-8 md:p-12 shadow-sm">
                        <button className="absolute top-6 left-6 w-10 h-10 rounded-full bg-white/80 dark:bg-gray-800/80 backdrop-blur-md flex items-center justify-center text-gray-400 hover:text-primary transition-colors border border-gray-100 dark:border-gray-700 z-10 shadow-sm">
                            <Search size={18} />
                        </button>

                        {selectedImage ? (
                            <motion.img
                                key={selectedImage}
                                initial={{ opacity: 0, scale: 0.95 }}
                                animate={{ opacity: 1, scale: 1 }}
                                transition={{ duration: 0.4 }}
                                alt={product.title}
                                className="max-w-full max-h-full object-contain group-hover:scale-105 transition-transform duration-700"
                                src={selectedImage}
                            />
                        ) : (
                            <div className="text-gray-300 flex flex-col items-center gap-2">
                                <Search size={32} />
                                <span className="text-sm font-medium">Pas d'image</span>
                            </div>
                        )}
                    </div>
                </div>

                {/* Product Info Section */}
                <div className="lg:col-span-5 lg:sticky lg:top-32 self-start space-y-10">

                    {/* Language Switcher */}
                    <div className="flex gap-2 justify-end">
                        {(["en", "fr", "tr"] as Language[]).map((l) => (
                            <button
                                key={l}
                                onClick={() => setLang(l)}
                                className={`text-xs uppercase px-3 py-1.5 rounded-lg border transition ${lang === l
                                    ? "bg-gray-900 text-white border-gray-900 dark:bg-white dark:text-gray-900"
                                    : "text-gray-500 border-gray-200 hover:border-gray-400"
                                    }`}
                            >
                                {LANG_FLAGS[l]} {l}
                            </button>
                        ))}
                    </div>

                    <header className="space-y-2">
                        <p className="text-xs uppercase tracking-[0.3em] text-primary font-semibold">ExclusivitÃ©s</p>
                        <h1 className="text-5xl md:text-6xl font-display leading-tight text-gray-900 dark:text-white">
                            {product.title}
                        </h1>
                        {product.subtitle && (
                            <p className="text-xl font-light text-gray-500 dark:text-gray-400 tracking-wide uppercase">
                                {product.subtitle}
                            </p>
                        )}
                    </header>

                    <div className="text-gray-600 dark:text-gray-400 leading-relaxed font-light text-base whitespace-pre-line">
                        <p>{product.description[lang]}</p>
                    </div>

                    {/* Features Grid */}
                    {product.features.length > 0 && (
                        <div className="grid grid-cols-2 sm:grid-cols-3 gap-8 py-10 border-y border-gray-100 dark:border-gray-800/50">
                            {product.features.map((f, i) => (
                                <div key={i} className="flex flex-col items-center text-center group">
                                    <div className="w-20 h-20 mb-6 flex items-center justify-center grayscale group-hover:grayscale-0 transition-all duration-700 opacity-80 group-hover:opacity-100 transform group-hover:scale-110">
                                        {f.image ? (
                                            <img src={f.image} alt={f.name} className="max-w-full max-h-full object-contain" />
                                        ) : (
                                            <Sparkles size={28} className="text-gray-300" />
                                        )}
                                    </div>
                                    <span className="text-sm md:text-base font-display text-gray-800 dark:text-gray-200 group-hover:text-primary transition-colors leading-tight">
                                        {f.name}
                                    </span>
                                </div>
                            ))}
                        </div>
                    )}

                    {/* Dimensions Section */}
                    {product.dimensions && (product.dimensions.totalHeight || product.dimensions.mattressHeight || product.dimensions.baseHeight) && (
                        <div className="pt-10 border-t border-gray-200 dark:border-gray-800">
                            <h3 className="text-[10px] uppercase tracking-[0.3em] font-bold mb-8 text-gray-500 dark:text-gray-400">
                                SpÃ©cifications Techniques
                            </h3>

                            <div className="grid grid-cols-1 gap-10">

                                {/* Individual Dimensions Grid */}
                                <div className="grid grid-cols-3 gap-4 md:gap-6">
                                    {(['totalHeight', 'mattressHeight', 'baseHeight'] as const).map((key) => {
                                        const val = product.dimensions?.[key];
                                        const img = product.dimensions?.[`${key}Image` as keyof typeof product.dimensions];
                                        const labels = { totalHeight: "Totale", mattressHeight: "Matelas", baseHeight: "Base" };

                                        if (!val) return null;

                                        return (
                                            <div key={key} className="flex flex-col group">
                                                <div className="aspect-square bg-white dark:bg-gray-200 rounded-2xl flex items-center justify-center mb-5 border border-gray-100 dark:border-gray-700/50 transition-all duration-300 p-3 overflow-hidden">
                                                    {img ? (
                                                        <img src={img as string} alt="" className="w-full h-full object-contain filter dark:brightness-90 group-hover:scale-110 transition-transform duration-500" />
                                                    ) : (
                                                        <div className="transform group-hover:scale-110 transition-transform duration-500">
                                                            {key === 'totalHeight' && (
                                                                <svg width="40" height="40" viewBox="0 0 100 100" fill="none" className="text-gray-400 dark:text-gray-500 group-hover:text-primary transition-colors">
                                                                    <path d="M20 75L50 85L80 75L50 65L20 75Z" stroke="currentColor" strokeWidth="1.5" />
                                                                    <path d="M20 75V60L50 70V85M80 75V60L50 70" stroke="currentColor" strokeWidth="1.5" />
                                                                    <path d="M20 60L50 70L80 60L50 50L20 60Z" stroke="currentColor" strokeWidth="1.5" strokeLinejoin="round" />
                                                                    <path d="M20 60V25H30V52" stroke="currentColor" strokeWidth="1.5" />
                                                                    <path d="M12 25H8M12 85H8M10 25V85" stroke="currentColor" strokeWidth="1" strokeDasharray="2 2" />
                                                                </svg>
                                                            )}
                                                            {key === 'mattressHeight' && (
                                                                <svg width="40" height="40" viewBox="0 0 100 100" fill="none" className="text-gray-400 dark:text-gray-500 group-hover:text-primary transition-colors">
                                                                    <path d="M20 75L50 85L80 75L50 65L20 75Z" stroke="currentColor" strokeWidth="1" opacity="0.3" />
                                                                    <path d="M20 60L50 70L80 60L50 50L20 60Z" stroke="currentColor" strokeWidth="2" fill="currentColor" fillOpacity="0.1" />
                                                                    <path d="M20 60V50L50 60V70L20 60Z" stroke="currentColor" strokeWidth="1.5" />
                                                                    <path d="M50 50L80 40M20 50L50 60L80 50L50 40L20 50Z" stroke="currentColor" strokeWidth="1.5" />
                                                                    <path d="M15 50H11M15 60H11M13 50V60" stroke="currentColor" strokeWidth="1" strokeDasharray="1 1" />
                                                                </svg>
                                                            )}
                                                            {key === 'baseHeight' && (
                                                                <svg width="40" height="40" viewBox="0 0 100 100" fill="none" className="text-gray-400 dark:text-gray-500 group-hover:text-primary transition-colors">
                                                                    <path d="M20 60L50 70L80 60L50 50L20 60Z" stroke="currentColor" strokeWidth="1" opacity="0.2" />
                                                                    <path d="M20 75L50 85L80 75L50 65L20 75Z" stroke="currentColor" strokeWidth="2" fill="currentColor" fillOpacity="0.1" />
                                                                    <path d="M20 75V60L50 70V85L20 75Z" stroke="currentColor" strokeWidth="1.5" />
                                                                    <path d="M15 60H11M15 75H11M13 60V75" stroke="currentColor" strokeWidth="1" strokeDasharray="1 1" />
                                                                </svg>
                                                            )}
                                                        </div>
                                                    )}
                                                </div>
                                                <div className="text-center">
                                                    <span className="block text-xl md:text-2xl font-bold text-gray-900 dark:text-white mb-1 group-hover:text-primary transition-colors duration-300">
                                                        {val}
                                                    </span>
                                                    <span className="block text-[9px] uppercase tracking-[0.2em] text-gray-400 font-semibold">
                                                        {labels[key]}
                                                    </span>
                                                </div>
                                            </div>
                                        );
                                    })}
                                </div>
                            </div>
                        </div>
                    )}

                    {/* Variants / Prices */}
                    <div className="space-y-6 pt-6 border-t border-gray-200 dark:border-gray-800">
                        <div>
                            <h3 className="text-xs uppercase tracking-[0.2em] font-semibold mb-6 text-gray-900 dark:text-white">
                                Dimensions &amp; Prix
                            </h3>

                            <div className={`grid grid-cols-1 ${product.variantsImage ? "lg:grid-cols-2 gap-12" : ""} items-start`}>
                                {product.variantsImage && (
                                    <div className="w-full aspect-square bg-white dark:bg-gray-200 rounded-2xl flex items-center justify-center p-8 border border-gray-100 dark:border-gray-700/50 overflow-hidden shadow-sm">
                                        <img
                                            src={product.variantsImage}
                                            alt="Guide des dimensions"
                                            className="max-w-full max-h-full object-contain filter dark:brightness-90 hover:scale-110 transition-transform duration-700"
                                        />
                                    </div>
                                )}

                                <div className="space-y-4">
                                    {product.variants.map((v, i) => (
                                        <div key={i} className="flex justify-between items-center text-sm font-light text-gray-600 dark:text-gray-400 border-b border-gray-100 dark:border-gray-800/50 pb-4">
                                            <div className="flex items-center gap-4">
                                                <span className="font-medium text-gray-800 dark:text-gray-200">{v.size}</span>
                                            </div>
                                            <div className="flex gap-4 items-center">
                                                {v.promoPrice && v.promoPrice < v.price && (
                                                    <span className="line-through text-gray-400 text-xs text-right">
                                                        â‚¬ {v.price.toLocaleString("fr-FR", { minimumFractionDigits: 2 })}
                                                    </span>
                                                )}
                                                <span className={`font-bold text-base min-w-[100px] text-right ${v.promoPrice && v.promoPrice < v.price ? "text-primary" : "text-gray-900 dark:text-white"}`}>
                                                    â‚¬ {(v.promoPrice && v.promoPrice < v.price ? v.promoPrice : v.price).toLocaleString("fr-FR", { minimumFractionDigits: 2 })}
                                                </span>
                                            </div>
                                        </div>
                                    ))}
                                </div>
                            </div>
                        </div>
                    </div>

                    {/* Accessories */}
                    {product.accessories && product.accessories.length > 0 && (
                        <div className="pt-4 border-t border-gray-200 dark:border-gray-800">
                            <h3 className="text-xs uppercase tracking-[0.2em] font-semibold mb-3 text-gray-900 dark:text-white">
                                Accessories
                            </h3>
                            <div className="space-y-2">
                                {product.accessories.map((acc, i) => (
                                    <div key={i} className="flex justify-between items-center p-3 bg-gray-50 dark:bg-gray-800/50 rounded-xl border border-gray-100 dark:border-gray-800/50">
                                        <div className="flex items-center gap-3">
                                            {acc.image && (
                                                <div className="w-10 h-10 rounded-lg overflow-hidden bg-white shrink-0">
                                                    <img src={acc.image} alt="" className="w-full h-full object-cover" />
                                                </div>
                                            )}
                                            <span className="text-sm font-medium text-gray-800 dark:text-gray-200">{acc.name[lang] || acc.name.en}</span>
                                        </div>
                                        <div className="flex gap-3 items-center">
                                            {acc.promoPrice && acc.promoPrice < acc.price && (
                                                <span className="line-through text-gray-400 text-xs">â‚¬ {acc.price.toLocaleString("fr-FR", { minimumFractionDigits: 2 })}</span>
                                            )}
                                            <span className={`font-semibold text-sm ${acc.promoPrice && acc.promoPrice < acc.price ? "text-primary" : "text-gray-900 dark:text-white"}`}>
                                                â‚¬ {(acc.promoPrice && acc.promoPrice < acc.price ? acc.promoPrice : acc.price).toLocaleString("fr-FR", { minimumFractionDigits: 2 })}
                                            </span>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>
                    )}

                    <div className="flex flex-col sm:flex-row gap-4 pt-8">
                        <button className="flex-1 bg-primary text-white py-5 px-8 text-xs uppercase tracking-[0.2em] font-semibold hover:bg-opacity-80 transition-all rounded-lg flex items-center justify-center gap-2">
                            Demander des Infos
                        </button>
                        <Link
                            href="/showroom"
                            className="flex-1 text-center border border-primary text-primary py-5 px-8 text-xs uppercase tracking-[0.2em] font-semibold hover:bg-primary hover:text-white transition-all rounded-lg"
                        >
                            Visiter le Showroom
                        </Link>
                    </div>
                </div>
            </section>
        </motion.div>
    );
}
